import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { selectAllPosts, PostedData, deletePostWithSinglePostArgument } from '../../features/postSlice'
import { selectAllUsers } from '../../features/userSlice'
import { selectAllCategories } from '../../features/categorySlice'
import { Avatar } from '@material-ui/core'
import history from '../../history'

const AdminPostDetail: React.FC = ({ match }: any) => {
  const { id } = match.params

  const posts = useSelector(selectAllPosts)
  const categories = useSelector(selectAllCategories)
  const users = useSelector(selectAllUsers)

  const dispatch = useDispatch()

  const singlePost: PostedData = posts.find((post : { _id: string }) => post._id === id)

  const matchCategoriesIdAndCategoriesName = (categoryId: string) => {
    return categories.find((category: { _id: string }) => category._id === categoryId)?.name
  }

  const findAuthorName = (uid: string) => {
    return users.find((user: { uid: string }) => user.uid === uid)?.username
  }

  const onDeletePostClicked = async (singlePost: PostedData) => {
    if (window.confirm('è¨˜äº‹ã‚’å‰Šé™¤ã—ã¦ã‚ˆã‚ã—ã„ã§ã™ã‹ï¼Ÿ')) {
      dispatch(deletePostWithSinglePostArgument(singlePost))
      history.push('/admin/post')
    }
  }
 
  return (
    <div>
      <h1>è¨˜äº‹è©³ç´°</h1>
      <p>Author: {findAuthorName(singlePost.uid)}</p>
      <h1>{singlePost.title}</h1>
      <p>{singlePost.text}</p>
      {singlePost.image && (
        <Avatar src={singlePost.image}/>
      )}
      <p>ã‚«ãƒ†ã‚´ãƒªãƒ¼ï¼š {matchCategoriesIdAndCategoriesName(singlePost.categoryId)}</p>
      <button onClick={() => onDeletePostClicked(singlePost)}>å‰Šé™¤</button>
    </div>
  )
}

export default AdminPostDetail
